import React, { useState } from "react";
import { NavLink } from "react-router-dom";

import { signInWithEmailAndPassword } from "firebase/auth";

import { auth } from "../../Firebase";

const Login = () => {
    const [erroMsg, setError] = useState("");
    const [Sucess,setSucess] = useState("");
    
  const [values, setValues] = useState({
    email: "",
    pass: "",
  });
// const [submitButtonDisabled, setSubmitButtonDisabled] = useState(false);
const [submitbuttondisable,setSubmitbuttondisable] = useState(false);

  const handleSubmit = () => {
    if (!values.email || !values.pass) {
      setError("Fill all fields");
    } else {
      setError("");
      setSubmitbuttondisable(true);
      signInWithEmailAndPassword(auth, values.email, values.pass)
        .then(async (res) => {
          setSubmitbuttondisable(false);
          const user = res.user;
          console.log(user);
          setSucess("Sucessfully Login");
          <NavLink to="/Home"></NavLink>;
          setValues({
            email: "",
            pass: "",
          });
        })
        .catch((err) => {
          setSubmitbuttondisable(false);
          setError(err.message);
        });
    }
  };

  return (
    <>
      {/* <!-- Button trigger modal --> */}
      <button
        type="button"
        className="btn btn-outline-primary ms-auto"
        data-bs-toggle="modal"
        data-bs-target="#loginModal"
      >
        <span className="fa fa-sign-in me-1"></span> Login
      </button>

      {/* <!-- Modal --> */}
      <div
        className="modal fade"
        id="loginModal"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                Login
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>

            <div className="modal-body">
              <button className="btn btn-primary w-100 mb-4">
                <span className="fa fa-google me-2"></span> Sign in With Google
              </button>

              <form>
                <div className="mb-3">
                  <label htmlFor="exampleInputEmail1" className="form-label">
                    Email address
                  </label>
                  <input 
                   type="email"
                   onChange={(event) =>
                     setValues((prev) => ({
                       ...prev,
                       email: event.target.value,
                     }))}
                    className="form-control"
                    aria-describedby="emailHelp"
                  />
                  <div id="emailHelp" className="form-text">
                    We'll never share your email with anyone else.
                  </div>
                </div>
                <div className="mb-3">
                  <label htmlFor="exampleInputPassword1" className="form-label">
                    Password
                  </label>
                  <input
                  
                  type="password"
                  onChange={(event) =>
                    setValues((prev) => ({
                      ...prev,
                      pass: event.target.value,
                    }))
                  }
                    className="form-control"
                    
                  />
                </div>
                
                <div className="mb-3" style={{ color: "red", fontsize: 30 }}>
                  {erroMsg}
                </div>
                <div className="mb-3" style={{ color: "green", fontsize: 30 }}>
                  {Sucess}
                </div>
                {/* <div className="mb-3 form-check">
                  <input
                    type="checkbox"
                    className="form-check-input"
                    id="exampleCheck1"
                  />
                  <label className="form-check-label" htmlFor="exampleCheck1">
                    Check me out
                  </label> */}
                {/* </div> */}
                <button
                  type="submit"
                  className="btn btn-outline-primary w-100 mt-5" 
                  onClick={handleSubmit}
                  disabled={submitbuttondisable}
                >
                  Submit
                </button>
                <div className="p-4 box mt-3 text-center">
                  {/* Don't have an Account Go To <Link to="/Home">Signup</Link> */}
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Login;

