
import React, { useState } from "react";
import { NavLink } from "react-router-dom";

import { createUserWithEmailAndPassword,updateProfile } from "firebase/auth";
import { auth } from "../../Firebase";

const Signup = () => {
  
  const [erroMsg, setError] = useState("");
  const [Sucess,setSucess] = useState("");
  
  const [submitbuttondisable,setSubmitbuttondisable] = useState(false);
  const [values, setvalues] = useState({
    name:"",
    email: "",
    pass: "",
  });
  // const [Submitbuttondisabled,setSubmitbuttondisable] = useState(false);
  
  const handleSubmit = () => {
    if (!values.name || !values.email || !values.pass) {
      setError("Fill All fields");
      
    }
    else
    {
        setError("");
        setSubmitbuttondisable(true);
        createUserWithEmailAndPassword(auth, values.email, values.pass)
          .then((res) => {
            setSubmitbuttondisable(false)
            const user =res.user;
            updateProfile(user,{
              displayName:values.name
            });
            console.log(user);
            setSucess("Sucessfully Created");
            <NavLink to="/Home"></NavLink>
            setvalues({
                email:"",
                pass:""
            });
          })
          .catch((err) => {
            setSubmitbuttondisable(false);
            setError(err.message)
    
          });
          console.log(values);
    }
    };

  return (
    <div>
      {/* <!-- Button trigger modal --> */}
      <button
        type="button"
        className="btn btn-outline-primary ms-2"
        data-bs-toggle="modal"
        data-bs-target="#signupModal"
      >
        <span className="fa fa-user-plus me-1"></span> Signup
      </button>

      {/* <!-- Modal --> */}
      <div
        className="modal fade"
        id="signupModal"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                Signup
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">
              {/* <button className="btn btn-primary w-100 mb-4">
                                <span className="fa fa-google me-2"></span> Sign up With Google
                            </button>  */}

              <form>
                {/* <div className="mb-3">
                                    <label htmlFor="exampleInput" className="form-label">Username</label>
                                    <input type="text" className="form-control" id="exampleInput" />
                                </div> */}
                <div className="mb-3">
                  <label htmlFor="exampleInputEmail1" className="form-label">
                    UserName
                  </label>
                  <input
                    type="text"
                    onChange={(event) =>
                      setvalues((prev) => ({
                        ...prev,
                        name: event.target.value,
                      }))
                    }
                    className="form-control"
                    aria-describedby="emailHelp"
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="exampleInputEmail1" className="form-label">
                    Email address
                  </label>
                  <input
                    type="email"
                    onChange={(event) =>
                      setvalues((prev) => ({
                        ...prev,
                        email: event.target.value,
                      }))
                    }
                    className="form-control"
                    aria-describedby="emailHelp"
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="exampleInputPassword1" className="form-label">
                    Password
                  </label>
                  <input
                    type="password"
                    onChange={(event) =>
                      setvalues((prev) => ({
                        ...prev,
                        pass: event.target.value,
                      }))
                    }
                    className="form-control"
                  />
                </div>
                <div className="mb-3" style={{ color: "red", fontsize: 30 }}>
                  {erroMsg}
                </div>
                <div className="mb-3" style={{ color: "green", fontsize: 30 }}>
                  {Sucess}
                </div>
                <div className="container">
                  <div className="row">
                    <button
                      onClick={handleSubmit}
                      disabled={submitbuttondisable}
                      className="btn btn-outline-primary mb-5 w-25 mx-auto"
                    >
                      Submit
                    </button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Signup;

